// ---------- MAIN APP ----------
export default function PaymentApp() {
  const dotRef = useRef<HTMLDivElement>(null);

  const [selected, setSelected] = useState<number | null>(null);
  const [showPopup, setShowPopup] = useState<number>(-1);
  const [showCheckout, setShowCheckout] = useState<boolean>(false);
  const [showCheckoutModal, setShowCheckoutModal] = useState<boolean>(false);

  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY;
      const docHeight = document.body.scrollHeight - window.innerHeight;
      const scrollProgress = (scrollTop / docHeight) * 100;
      const bar = document.getElementById("progress-bar");
      if (bar) bar.style.width = `${scrollProgress}%`;
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    const lenis = new Lenis({
      duration: 1.2,
      easing: (t: number) => Math.min(1, 1.001 - Math.pow(2, -10 * t)),
      smoothWheel: true,
    });
    const raf = (time: number) => {
      lenis.raf(time);
      requestAnimationFrame(raf);
    };
    requestAnimationFrame(raf);

    if (typeof window !== "undefined" && window.innerWidth >= 1024) {
      let mouseX = 0,
        mouseY = 0,
        currentX = 0,
        currentY = 0;
      const updateMouse = (e: MouseEvent) => {
        mouseX = e.clientX;
        mouseY = e.clientY;
      };
      const followCursor = () => {
        currentX += (mouseX - currentX) * 0.25;
        currentY += (mouseY - currentY) * 0.25;
        if (Math.abs(currentX - mouseX) < 0.2) currentX = mouseX;
        if (Math.abs(currentY - mouseY) < 0.2) currentY = mouseY;
        if (dotRef.current) {
          dotRef.current.style.transform = `translate3d(${currentX}px, ${currentY}px, 0) translate(-50%, -50%)`;
        }
        requestAnimationFrame(followCursor);
      };
      window.addEventListener("mousemove", updateMouse);
      followCursor();

      window.addEventListener("mousedown", (e: MouseEvent) => {
        const dot = dotRef.current;
        if (!dot) return;
        const ripple = document.createElement("div");
        ripple.className = "cursor-ripple";
        ripple.style.left = `${e.clientX - 8}px`;
        ripple.style.top = `${e.clientY - 8}px`;
        document.body.appendChild(ripple);
        setTimeout(() => ripple.remove(), 400);
      });
      return () => {
        window.removeEventListener("mousemove", updateMouse);
        lenis.destroy();
      };
    }
  }, []);

  useEffect(() => {
    const canvas = document.getElementById("glowAurora") as HTMLCanvasElement;
    const ctx = canvas.getContext("2d");
    let w = (canvas.width = window.innerWidth);
    let h = (canvas.height = window.innerHeight);

    let colors = ["#ff00cc", "#00e0ff", "#aaff00", "#ffcc00", "#aa00ff"];
    let blobs = Array.from({ length: 12 }, () => ({
      x: Math.random() * w,
      y: Math.random() * h,
      dx: (Math.random() - 0.5) * 1.5,
      dy: (Math.random() - 0.5) * 1.5,
      r: Math.random() * 80 + 80,
      color: colors[Math.floor(Math.random() * colors.length)],
    }));

    let mouse = { x: w / 2, y: h / 2 };
    window.addEventListener("mousemove", (e) => {
      mouse.x = e.clientX;
      mouse.y = e.clientY;
    });

    function draw() {
      if (!ctx) return;

      ctx.clearRect(0, 0, w, h);

      blobs.forEach((b) => {
        b.x += b.dx + (mouse.x - w / 2) * 0.0009;
        b.y += b.dy + (mouse.y - h / 2) * 0.0009;

        if (b.x < -b.r || b.x > w + b.r) b.dx *= -1;
        if (b.y < -b.r || b.y > h + b.r) b.dy *= -1;

        let gradient = ctx.createRadialGradient(b.x, b.y, 0, b.x, b.y, b.r);
        gradient.addColorStop(0, `${b.color}88`); // more opacity
        gradient.addColorStop(1, `${b.color}00`);

        ctx.beginPath();
        ctx.fillStyle = gradient;
        ctx.arc(b.x, b.y, b.r, 0, Math.PI * 2);
        ctx.fill();
      });

      requestAnimationFrame(draw);
    }

    draw();

    window.addEventListener("resize", () => {
      w = canvas.width = window.innerWidth;
      h = canvas.height = window.innerHeight;
    });
  }, []);

  return (
    <motion.main
      className="pricing-page"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.65 }}
    >
      <div className="progress-bar" id="progress-bar"></div>
      {typeof window !== "undefined" && window.innerWidth >= 1024 && (
        <div className="cursor-dot" ref={dotRef}></div>
      )}

      <canvas id="glowAurora" className="aurora-canvas"></canvas>
      <header className="pricing-header">
        <h1>
          Boost your <span className="highlight">Content Performance</span>
        </h1>
        <p className="micro">
          WeeklyFlow = 1 video/day  1 Slot = 1 video edit
        </p>
      </header>
      <h2 className="section-heading"> Weekly Retainer Plan</h2>
      <motion.section
        className="section-row animated-container"
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true, amount: 0.2 }}
      >
        <PlanCard
          plan={plans[0]}
          index={0}
          active={selected === 0}
          setActive={(i: number) => setSelected(i)}
          onGetStarted={() => {
            setSelected(0);
            setShowCheckout(true);
          }}
          showPopup={showPopup}
          setShowPopup={setShowPopup}
        />
        <InfoCard data={infoCards[0]} />
      </motion.section>
      <h2 className="section-heading"> Credit-Based Plans</h2>
      <motion.section
        className="section-row animated-container"
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true, amount: 0.2 }}
      >
        {plans.slice(1, 3).map((plan, i) => (
          <PlanCard
            plan={plan}
            index={i + 1}
            active={selected === i + 1}
            setActive={(i2: number) => setSelected(i2)}
            showPopup={showPopup}
            setShowPopup={setShowPopup}
            onGetStarted={() => {
              setSelected(i + 1);
              setShowCheckout(true);
            }}
            key={plan.name}
          />
        ))}
      </motion.section>
      <h2 className="section-heading"> One-Time Edit + Slot System</h2>
      <motion.section
        className="section-row animated-container"
        initial="hidden"
        whileInView="visible"
        viewport={{ once: true, amount: 0.2 }}
      >
        <InfoCard data={infoCards[1]} />
        <PlanCard
          plan={plans[3]}
          index={3}
          active={selected === 3}
          setActive={() => setSelected(3)}
          showPopup={showPopup}
          setShowPopup={setShowPopup}
          onGetStarted={() => {
            setSelected(3);
            setShowCheckout(true);
          }}
        />
      </motion.section>
      <FooterTrust />
      <AnimatePresence>
        {selected != null && showCheckout && (
          <StickyCheckoutBar
            plan={plans[selected]}
            onConfirm={() => {
              setShowCheckout(false);
              setTimeout(() => {
                setShowCheckoutModal(true);
              }, 300);
            }}
          />
        )}
      </AnimatePresence>
      <ContinueCheckoutModal
        open={showCheckoutModal}
        onClose={() => setShowCheckoutModal(false)}
        checkoutUrl={selected != null ? plans[selected].checkoutUrl : "#"}
      />
      <footer className="pricing-footer">
        <p>
          Start editing today. Secure payments. Unlimited revisions included.
        </p>
      </footer>
    </motion.main>
  );
}
